'use strict';
const shareDataError = 'shareData config is not correct';
const wxLoadError = 'wx load is not correct';
const wxConfigError = 'wx config is not correct';
const shareType = obj => {
  let shareItems = [], specialContent = {}, friendContent = '', qqContent = '',
    sinaContent = '';
  if (obj || GOKU) {
    let shareObj = obj || GOKU.shareObj;
    if (shareObj) {
      if (shareObj.content instanceof Array) {
        const url = shareObj.jumpUrl ? shareObj.jumpUrl !== '' ? shareObj.jumpUrl : location.href : location.href;
        shareObj.content.map(item => {
          switch (item.channel) {
            case "WEIXIN_FRIEND":
              specialContent['wxhy'] = item.content;
              friendContent = item.content;
              shareItems.push("wxhy");
              break;
            case "WEIXIN_FRIEND_CIRCLE":
              specialContent['wxpyq'] = item.content;
              shareItems.push("wxpyq");
              break;
            case "QQ_FRIEND":
              specialContent['qqhy'] = item.content;
              shareItems.push("qqhy");
              break;
            case "QQ_WEIBO":
              specialContent['tcwb'] = item.content + url;
              shareItems.push("tcwb");
              qqContent = item.content;
              break;
            case "SINA_WEIBO":
              specialContent['sinawb'] = item.content + url;
              shareItems.push("sinawb");
              sinaContent = item.content;
              break;
            case  "SMS":
              specialContent['sms'] = item.content + url;
              shareItems.push("sms");
              break;
          }
        });
        specialContent['qqhy_url'] = url;
        specialContent['wx_url'] = url;
      } else {
        throw new Error(shareDataError)
      }
    }
    else {
      throw new Error(shareDataError)
    }
  } else {
    throw new Error(shareDataError)
  }
  return {shareItems, specialContent, friendContent, qqContent, sinaContent}
};

export {shareType, shareDataError, wxLoadError, wxConfigError};